<template>
  <div class="mobile-container">
    <Header/>
    <div class="m-content">
      <div class="m-head">
        <div class="m-item">
          <router-link tag="a" :to="{path:'/tokenone'}"><img src="@/assets/img/back.png" alt="">
          </router-link>
        </div>
      </div>
      <div id="search_carseries" class="search_carseries">
        <div class="search_carseries_wrap">
          <div class="search">
            <div class="search_input">
              <input type="text" v-model="searchval" @input.self="autosearch(searchval)" :placeholder="$t('lang.token_input')"/>
            </div>
          </div>
          <div class="search_carseries_inner"></div>
          <div class="carseries_list_wrap">
            <div class="carseries_list" v-for="(item,index) in oldlist" :key="index" >
              <input type="hidden" :id="item.token" :value="item.name"  name="seriescar">
              <span>{{ item.img }}</span>
              <label :for="item.token" @click="Selected(item.token)"><span>{{item.name}}</span></label>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
  import Header from '@/components/MHeader'
  import Footer from '@/components/MFooter'

  export default {
    components: {
      Header,
      Footer,
    },
    data() {
      return {
        address:"",
        searchval:"",
        newlist:[],
        oldlist:[]
      }
    },
    created() {
      this.address = this.$route.query.address;
    },
    computed: {},
    mounted() {
      this.getCarSeries()
    },
    methods: {
      autosearch(searchval) {
        this.oldlist = []
        for (const item of this.newlist){
          if(item.name.indexOf(searchval) !== -1 && item.name){
            this.oldlist.push(item)
          }
        }
      },
      getCarSeries() {
        this.$http.get("api/contract/token").then(res => {
          if (res.data.code === 200) {
            let data = []
            for (let item of res.data.data.data) {
              item["img"] = item.name.substring(0,1)
              data.push(item)
            }
            data.sort(function(x, y){
              return x.name.localeCompare(y.name);
            });
            this.oldlist = data
            this.newlist = data
          }
        })
      },
      Selected(val) {
        this.$router.push({path:'/tokenone', query:{token: val, address: this.address}})
      }
    }
  }


</script>


<style scoped>
  .m-content {
    padding: 1rem;
    background: #E6E9EE;
  }

  .m-head {
    background: #ffffff;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-top: 17px;
    padding-left: 19px;
  }
  .search_carseries {
    background: #ffffff;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-top: 21px;
    padding-left: 19px;
    padding-bottom: 100px;
  }
  .search_carseries .search input {
    width:313px;
    height:14px;
    background:rgba(255,255,255,1);
    border:1px solid rgba(230,230,230,1);
    opacity:1;
    border-radius:4px;
    font-size:12px;
    padding: 17px 8px;
  }
  .search_carseries .carseries_list_wrap {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .search_carseries .carseries_list > span {
    width: 18px;
    height: 18px;
    background-color: #0E932E;
    border-radius: 18px;
    display: inline-block;
    text-align: center;
    line-height: 18px;
    color: #ffffff !important;
    font-size: 12px !important;
    /*position: absolute;*/
    /*z-index: 10;*/
    /*top: 11px;*/
    /*left: 20px;*/
  }
  .search_carseries .carseries_list_wrap img {
    width: 18px;
    height: 18px;
    vertical-align: middle;
  }
  .search_carseries .carseries_list {
    margin-top: 36px;
  }
  .search_carseries .carseries_list_wrap span {
    font-size:16px;
    color:rgba(0,0,0,1);
    opacity:1;
    margin-left: 16px;
  }


</style>



